import React, {useState} from 'react'
import ReactCardFlip from 'react-card-flip'
import { FaArrowRight } from 'react-icons/fa';

export default function ItemFormacion(props) {

  const {nombre, descr, anio, ubicacion, img, icon} = props.item;
  
  const [isFlipped, setFlipped] = useState(false);

  const flipCard = (e) => {
    e.preventDefault();
    setFlipped(prevState => ({
      isFlipped: !prevState
    }));
  }

  return (
    <ReactCardFlip isFlipped={isFlipped} infinite={true}>

      <div className="h-40 w-80 bg-greenEmerald-dark rounded-lg shadow-lg grid grid-rows-3">
        <div className="grid grid-cols-3 rounded-t-lg row-span-2">
          <div className="m-1 col-span-1 flex items-center justify-center">
            {img ? <img className="h-15 w-20 rounded-lg" src={`../assets/${img}`} alt={`Logo de ${nombre}`} /> : 
              <div className=" flex justify-items-center justify-center place-items-center rounded-full bg-greenEmerald w-10 h-10">
                {icon}
              </div>
            }
          </div>
          <div className="col-span-2 pt-2 pl-2">
            <div className="flex justify-between">
              <p className="font-poppins text-white font-semibold text-base">{nombre}</p>
              <p className="font-poppins text-gray-200 font-semibold text-xs px-2">{anio}</p>
            </div>
            <p className="font-poppins text-gray-200 font-medium text-sm">{ubicacion}</p>
          </div>
        </div>
        <div className="rounded-b-lg row-span-1 flex place-items-center justify-end px-2">
          <button className="rounded-full h-10 w-10 flex items-center justify-center bg-linkedinColor" onClick={flipCard} title="Girar"> <FaArrowRight size="30" color="white"/></button>
        </div>
        
      </div>

      <div className="h-40 w-80 bg-greenEmerald-dark rounded-lg shadow-lg grid grid-rows-3">
        <div className="rounded-t-lg row-span-2 px-3 pt-3">
          <p className="font-poppins text-gray-200 font-semibold">Informaci√≥n</p>
          <p className="font-poppins text-white text-justify">{descr}</p>
        </div>
        <div className="rounded-b-lg row-span-1 flex place-items-center justify-end px-2">
          <button className="rounded-full h-10 w-10 flex items-center justify-center bg-linkedinColor" onClick={flipCard} title="Girar"><FaArrowRight size="30" color="white"/></button>
        </div>
        
      </div>        
    </ReactCardFlip>
  )
}
