import React from 'react';
import {BrowserRouter as Router, Route, Switch } from "react-router-dom";

import Appbar from './components/Appbar';
import ScrollTop from './components/ScrollTop';
import Footer from './components/Footer';

import Home from './paginas/Home';
import SobreMi from './paginas/Sobremi';
import Formacion from './paginas/Formacion';
import Conocimientos from './paginas/Conocimientos';
import Portfolio from './paginas/Portfolio';
import Contacto from './paginas/Contacto';
import Error404 from './paginas/404';

function App() {
  return (
    <Router>
      <Appbar/>
      <Switch>
        <Route exact path="/" component={Home}/>
        <Route exact path="/sobremi" component={SobreMi}/>
        <Route exact path="/formacion" component={Formacion}/>
        <Route exact path="/conocimientos" component={Conocimientos}/>
        <Route exact path="/portfolio" component={Portfolio}/>
        <Route exact path="/contacto" component={Contacto}/>
        <Route component={Error404}/>
      </Switch>
      <Footer/>
      <ScrollTop/>
    </Router>
  );
}

export default App;
