import React from "react";
import ItemFormacion from "../items/ItemFormacion";

import { complementario, academico } from "../data/data";

export default function Formacion() {
  return (
    <div className="h-auto pb-10 w-full bg-greenEmerald-light">
      <div className="h-screen py-60 bg-gray-500 bg-header-formacion bg-cover bg-blend-overlay text-center">
        <h3 className="text-2xl font-poppins text-white">
          En continuo aumento
        </h3>
        <h2 className="text-6xl font-poppins text-white font-medium md:text-8xl">
          Formaci√≥n
        </h2>
      </div>
      <div>
        <div className="text-center bg-greenEmerald mx-5 rounded-lg">
          <h4 className="text-2xl font-poppins text-white font-medium mt-4">
            Oficial
          </h4>
        </div>

        <div className="grid grid-cols-1 gap-3 pt-5 md:flex justify-items-center justify-center">
          {academico.map((item) => {
            return <ItemFormacion key={item.nombre} item={item}/>
          })}
        </div>

        <div className="text-center bg-greenEmerald mx-5 rounded-lg">
          <h4 className="text-2xl font-poppins text-white font-medium mt-4">
            Complementaria
          </h4>
        </div>

        <div className="grid grid-cols-1 gap-3 justify-center justify-items-center pt-5 md:pt-10 md:grid-cols-2 lg:grid-cols-3">
          {complementario.map((item) => {
            return <ItemFormacion key={item.nombre} item={item}/>
          })}
        </div>
      </div>
    </div>
  );
}
