import React, {useState, useEffect} from "react";
import { Disclosure, Transition} from "@headlessui/react";
import {Link, NavLink} from 'react-router-dom';
import {FiMenu} from 'react-icons/fi';
import {RiCloseCircleLine} from 'react-icons/ri';

export default function Appbar() {

  let navegacion = [
    {nombre: "Inicio", hacia: "/"},
    {nombre: "Sobre m√≠", hacia: "/sobremi"},
    {nombre: "Formaci√≥n", hacia: "/formacion"},
    {nombre: "Conocimientos", hacia: "/conocimientos"},
    {nombre: "Portfolio", hacia: "/portfolio"},
    {nombre: "Contacto", hacia: "/contacto"},
  ]

  const [changeColor, setChangeColor] = useState(false);

  useEffect(() => {
    window.addEventListener('scroll', checkScroll)
    return function cleanup(){
      window.removeEventListener('scroll', checkScroll)
    }
  });

  const checkScroll = () => {
    if(!changeColor && window.pageYOffset > 100){
      setChangeColor(true)
    }else if(changeColor && window.pageYOffset <= 100){
      setChangeColor(false)
    }
  }

  return (
    <Disclosure as="nav" className={`fixed w-full rounded-b-lg ${changeColor ? "bg-green-700" : "bg-transparent"} transition duration-500 z-40`}>
      {({ open }) => (
        <>
          <div className="max-w-7xl mx-auto px-2 sm:px-6 lg:px-8">
            <div className="relative flex items-center justify-between h-16">
              <div className="absolute inset-y-0 left-0 flex items-center sm:hidden">
                <Disclosure.Button className="inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-white hover:bg-green-600 focus:utline-none focus:ring-2 focus:ring-inset focus:ring-white">
                  <span className="sr-only">Abrir men√∫</span>
                  {open ? (
                    <RiCloseCircleLine className="block h-6 w-6" color="white" aria-hidden="true"/>
                  ) : (
                    <FiMenu className="block h-6 w-6" color="white" aria-hidden="true"/>
                  )}
                </Disclosure.Button>
              </div>
              <div className="flex-1 flex items-center justify-center sm:items-strech sm:justify-evenly">
                <div className="flex-shrink-0 flex items-center">
                  <a href="/" rel="noreferrer">
                    <div className={`h-10 w-10 bg-${changeColor ? "white" : "transparent"} rounded-full flex justify-items-center justify-center place-items-center transition duration-500`}>
                      <img className="block lg:block h-8 w-auto" src="../favicon.png" alt="Logo"></img>
                    </div>
                    
                  </a>
                </div>
                <div className="hidden sm:block sm:ml-6">
                  <div className="flex space-x-4">
                    {navegacion.map((item) => (
                      <NavLink key={item.nombre} to={item.hacia} exact={true} className={`font-poppins text-gray-100 hover:bg-green-600 hover:text-white px-3 py-2 rounded-md text-sm font-medium`} activeClassName="bg-green-900 text-white">{item.nombre}</NavLink>
                    ))}                  
                  </div>
                </div>
              </div>
            </div>
          </div>
          <Transition enter="transition duration-200 ease-out" enterFrom="transform scale-95 opacity-0" enterTo="transform scale-200 opacity-100" leave="transition duration-100 ease-out" leaveFrom="transform scale-100 opacity-100" leaveTo="transform scale-95 opacity-0">
            <Disclosure.Panel className="sm:hidden">
              <div className="px-2 pt-2 pb-3 space-y-1">
                {navegacion.map((item) => (
                  <Link key={item.nombre} to={item.hacia} exact={true} className={`font-poppins text-gray-100 bg-green-500 bg-opacity-75 hover:bg-green-600 hover:text-white block px-3 py-2 rounded-md text-sm font-medium`} activeClassName="bg-green-900 text-white">{item.nombre}</Link>
                ))}
              </div>
            </Disclosure.Panel>
          </Transition>

        </>
      )}
    </Disclosure>

  );
}
