import React from "react";
import { FaGithub } from "react-icons/fa";
import ItemPortfolio from "../items/ItemPortfolio";

import { portfolio } from "../data/data";

export default function Portfolio() {
  return (
    <div className="h-auto w-full pb-10 bg-greenEmerald-light">
      <div className="h-screen py-60 bg-gray-500 bg-header-portfolio bg-cover bg-center bg-blend-overlay text-center">
        <h3 className="text-2xl font-poppins text-white">
          Conoce mis proyectos
        </h3>
        <h2 className="text-6xl font-poppins text-white font-medium md:text-8xl">
          Portfolio
        </h2>
      </div>
      <div className="grid grid-cols-1 gap-3 justify-items-center pt-4 md:pt-10 md:grid-cols-2 lg:grid-cols-3">
        {portfolio.map((item) => {
          return <ItemPortfolio key={item.nombre} item={item} />;
        })}
      </div>
      <div className="text-center pt-6">
        <a
          className="font-poppins bg-white text-darkBlue text-sm px-3 py-2 rounded-full hover:bg-gray-200 transition duration-200 shadow-lg mt-28 md:mt-0"
          href="https://www.github.com/danielloroduran"
          rel="noreferrer"
          target="_blank"
        >
          <FaGithub className="inline mr-3" />
          ¬°Esto y m√°s en GitHub!
        </a>
      </div>
    </div>
  );
}
