import React from 'react'

export default function ItemConocimientos(props) {

  const {img, nombre} = props.item;

  return (
    <div className="rounded-full h-40 w-40 bg-green-500 grid grid-cols-1 place-content-center place-items-center text-white">
      <img className="h-20 w-20" src={`./assets/conocimientos/${img}`} alt={`Logo de ${nombre}`}></img>
      <p className="px-3 my-2 font-poppins text-center overflow-auto break-word">{nombre}</p>
    </div>
  )
}
