import React from 'react'

export default function ItemContacto(props) {
  return (
    <a className={`rounded-full h-40 w-40 ${props.bgcolor} shadow-lg grid grid-cols-1 place-content-center place-items-center text-white transition duration-500 hover:-translate-y-1 transform focus:outline-none focus:ring focus:ring-offset-2 focus:ring-${props.bgcolor}`} href={props.url} target="_blank" rel="noreferrer">
      {props.icon}
      <p className={`px-3 my-2 font-poppins text-center overflow-auto ${props.nombre.includes("@") ? "break-all" : "break-word"}`}>{props.nombre}</p>
    </a>
  )
}
