import React, {useState} from 'react'
import ReactCardFlip from 'react-card-flip';
import {FaGithub, FaArrowRight} from 'react-icons/fa';

export default function ItemPortfolio(props) {

  const {nombre, img, tecn, url, descr} = props.item;

  const [isFlipped, setFlipped] = useState(false);

  const flipCard = (e) => {
    e.preventDefault();
    setFlipped(prevState => ({
      isFlipped: !prevState
    }));
  }

  return (

    <ReactCardFlip isFlipped={isFlipped} infinite={true}>
      <div className="h-40 w-80 bg-greenEmerald-dark rounded-lg shadow-lg grid grid-rows-3">
        <div className="grid grid-cols-3 rounded-t-lg row-span-2">
          <div className="m-1 col-span-1 flex items-center justify-center">
            <img className="h-20 w-20 rounded-lg" src={`./assets/portfolio/${img}`} alt={`Logo de ${nombre}`} />
          </div>
          <div className="col-span-2 block pt-2 pl-2">
            <p className="font-poppins text-white font-semibold text-xl pr-1">{nombre}</p>
            <p className="font-poppins text-gray-200">{tecn}</p>
          </div>
        </div>
        <div className="rounded-b-lg row-span-1 flex place-items-center justify-between px-2">
          <a className="rounded-full h-10 w-10 bg-black flex items-center justify-center transition duration-500 hover:-translate-y-1 transform" href={`https://www.github.com/danielloroduran/${url}`} rel="noreferrer" target="_blank"><FaGithub size="30" color="white"/></a>
          <button className="rounded-full h-10 w-10 flex items-center justify-center bg-linkedinColor" onClick={flipCard} title="Girar"> <FaArrowRight size="30" color="white"/></button>
        </div>
      </div>
      
      <div className="h-40 w-80 bg-greenEmerald-dark rounded-lg shadow-lg grid grid-rows-3">
        <div className="rounded-t-lg row-span-2 px-3 pt-3">
          <p className="font-poppins text-gray-200 font-semibold">Informaci√≥n</p>
          <p className="font-poppins text-white text-justify">{descr}</p>
        </div>
        <div className="rounded-b-lg row-span-1 flex place-items-center justify-end px-2">
          <button className="rounded-full h-10 w-10 flex items-center justify-center bg-linkedinColor" onClick={flipCard} title="Girar"><FaArrowRight size="30" color="white"/></button>
        </div>
        
      </div>
      
    </ReactCardFlip>


  )
}
