import React, {useState, useEffect} from 'react'

import { FaArrowUp } from 'react-icons/fa';

export default function ScrollTop() {

  const [showScroll, setShowScroll] = useState(false);

  useEffect(() =>{
    window.addEventListener('scroll', checkScrollTop)
    return function cleanup(){
      window.removeEventListener('scroll', checkScrollTop)
    }
  });

  const checkScrollTop = () => {
    if (!showScroll && window.pageYOffset > 400){
      setShowScroll(true)
    }else if(showScroll && window.pageYOffset <= 400){
      setShowScroll(false)
    }
  };

  const scrollTop = () => {
    window.scrollTo({top: 0, behavior: 'smooth'});
  };

  return (
    <div className={`fixed h-12 w-12 bottom-5 right-5 items-center justify-center bg-green-600 cursor-pointer transition duration-500 rounded-full shadow-lg hover:bg-green-500 hover:-translate-y-0.5 transform active:bg-green-700 ${showScroll ? "flex" : "hidden"}`} onClick={scrollTop}>
      <FaArrowUp color="white"/>
    </div>
  )
}
